import React, {Component} from "react";
import Todo from "./todos"

class Form extends Component {
    constructor(props) {
        super(props);
        this.state = {
            list : [],
            currentItem : {
                item : "",
                key : ""
            }
        }
    }

    handleInput = (e) => {
        this.setState ({
            currentItem : {
                item : e.target.value,
                key : Date.now()
            }
        })

    }
    addItem = (e) => {
        e.preventDefault();
        const item = this.state.currentItem;
        if (item.item !=='') {
            const items = [...this.state.list, item];
            this.setState({                                                   
                list : items,
                currentItem : {
                    item : '',
                    key : ''
                }
            })
        }
        document.getElementById("getInput").value = ""
    }
    deleteItem = (key) => {
        const filteredItems = this.state.list.filter(item => item.key !== key);
        this.setState ({
            list : filteredItems
        })
    }
    render() {
        return (
            <div>
                <form onSubmit = {this.addItem}>
                    <input  id = "getInput" placeholder = "Add Tasks" className = "input" type = "text" onChange = {this.handleInput} value = {this.state.currentItem.text}></input>
                    <button className = "add_button btn btn-primary"  type = "submit">+</button>
                </form>
     
                {this.state.list.map(item => (
                        <Todo key = {item.key} text = {item.item} id = {item.key} deleteItem = {this.deleteItem}/>
                    
                ))}
            </div>
        )
    }
}

export default Form;