import React, {Component} from "react"

class Todo extends Component {
    render() {
        return (
            <div className = "todos_list">
                    <p className = "todos">
                         {this.props.text}
                        <button onClick = {() => this.props.deleteItem(this.props.id)} className = "btn btn-danger btn-sm remove_button">Remove</button>
                    </p>
            </div>
        )
    }
}

export default Todo;